					<view class="radio-group">
						<view 
							class="radio-item" 
							:class="{ active: formData.gender === 'male' }"
							@click="selectGender('male')"
						>
							<text class="radio-text">Áî∑</text>
						</view>
						<view 
							class="radio-item" 
							:class="{ active: formData.gender === 'female' }"
							@click="selectGender('female')"
						>
							<text class="radio-text">Â•≥</text>
						</view>
					</view>
				</view>
				
				<!-- Âá∫ÁîüÊó•Êúü -->
				<view class="form-item">
					<text class="label">Âá∫ÁîüÊó•Êúü</text>
					<picker 
						mode="date" 
						:value="formData.birthDate" 
						@change="onBirthDateChange"
						:end="today"
					>
						<view class="picker-view">
							<text class="picker-text" :class="{ placeholder: !formData.birthDate }">
								{{ formData.birthDate || 'ËØ∑ÈÄâÊã©Âá∫ÁîüÊó•Êúü' }}
							</text>
							<text class="picker-icon">üìÖ</text>
						</view>
					</picker>
				</view>
				
				<!-- Âπ¥ÈæÑÊòæÁ§∫ -->
				<view class="age-display" v-if="formData.birthDate">
					<text class="age-text">Âπ¥ÈæÑÔºö{{ calculatedAge }}</text>
					<text class="age-range-text">ËØÑ‰º∞ËåÉÂõ¥Ôºö{{ ageRangeText }}</text>
				</view>
			</view>
			
			<!-- ÂÆ∂Â∫≠‰ø°ÊÅØ -->
			<view class="form-section">
				<view class="section-title">
					<text class="title-text">ÂÆ∂Â∫≠‰ø°ÊÅØ</text>
				</view>
				
				<!-- ‰∏ªË¶ÅÁÖßÈ°æËÄÖ -->
				<view class="form-item">
					<text class="label">‰∏ªË¶ÅÁÖßÈ°æËÄÖ</text>
					<view class="radio-group">
						<view 
							class="radio-item" 
							:class="{ active: formData.caregiver === 'mother' }"
							@click="selectCaregiver('mother')"
						>
							<text class="radio-text">ÊØç‰∫≤</text>
						</view>
						<view 
							class="radio-item" 
							:class="{ active: formData.caregiver === 'father' }"
							@click="selectCaregiver('father')"
						>
							<text class="radio-text">Áà∂‰∫≤</text>
						</view>
						<view 
							class="radio-item" 
							:class="{ active: formData.caregiver === 'grandparent' }"
							@click="selectCaregiver('grandparent')"
