						type="number"
					/>
				</view>
			</view>
			
			<!-- ÂèëËÇ≤Áõ∏ÂÖ≥ -->
			<view class="form-section">
				<view class="section-title">
					<text class="title-text">ÂèëËÇ≤Áõ∏ÂÖ≥</text>
				</view>
				
				<!-- ÊòØÂê¶ÊúâÂèëËÇ≤ËøüÁºìÂè≤ -->
				<view class="form-item">
					<text class="label">ÊòØÂê¶ÊúâÂèëËÇ≤ËøüÁºìÂè≤</text>
					<view class="radio-group">
						<view 
							class="radio-item" 
							:class="{ active: formData.hasDelayHistory === 'no' }"
							@click="selectDelayHistory('no')"
						>
							<text class="radio-text">Êó†</text>
						</view>
						<view 
							class="radio-item" 
							:class="{ active: formData.hasDelayHistory === 'yes' }"
							@click="selectDelayHistory('yes')"
						>
							<text class="radio-text">Êúâ</text>
						</view>
					</view>
				</view>
				
				<!-- Â§áÊ≥® -->
				<view class="form-item">
					<text class="label">Â§áÊ≥®‰ø°ÊÅØ</text>
					<textarea 
						class="textarea" 
						v-model="formData.notes" 
						placeholder="Â¶ÇÊúâÂÖ∂‰ªñÈúÄË¶ÅËØ¥ÊòéÁöÑÊÉÖÂÜµÔºåËØ∑Âú®Ê≠§Â°´ÂÜô"
						placeholder-style="color: #BDC3C7"
						maxlength="200"
					/>
					<text class="char-count">{{ formData.notes.length }}/200</text>
				</view>
			</view>
		</view>
		
		<!-- Â∫ïÈÉ®ÊåâÈíÆ -->
		<view class="button-container">
			<button class="next-button" @click="goToAssessment" :disabled="!isFormValid">
				<text class="button-text">ÂºÄÂßãËØÑ‰º∞</text>
			</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				formData: {
					name: '',
					gender: '',
					birthDate: '',
					caregiver: '',
					phone: '',
					hasDelayHistory: '',
					notes: ''
				},
				today: ''
			}
		},
		computed: {
			// ËÆ°ÁÆóÂπ¥ÈæÑ
			calculatedAge() {
				if (!this.formData.birthDate) return ''
				const birth = new Date(this.formData.birthDate)
				const today = new Date()
				let age = today.getFullYear() - birth.getFullYear()
				const monthDiff = today.getMonth() - birth.getMonth()
				
				if (monthDiff < 0 || (monthDiff === 0 && today.getDate() < birth.getDate())) {
					age--
				}
				
				if (age < 1) {
					const months = Math.floor((today - birth) / (1000 * 60 * 60 * 24 * 30))
					return `${months}‰∏™Êúà`
				}
				
				return `${age}Â≤Å`
			},
			
			// ËÆ°ÁÆóÂπ¥ÈæÑÊÆµÊñáÊú¨
			ageRangeText() {
				if (!this.formData.birthDate) return ''
				const ageInMonths = this.calculateAgeInMonths(this.formData.birthDate)
				return this.getAgeRangeText(ageInMonths)
			},
			
			// Ë°®ÂçïÈ™åËØÅ
			isFormValid() {
				return this.formData.name && 
					   this.formData.gender && 
					   this.formData.birthDate && 
					   this.formData.caregiver && 
					   this.formData.phone
			}
		},
		onLoad() {
			// ËÆæÁΩÆ‰ªäÂ§©ÁöÑÊó•Êúü‰Ωú‰∏∫ÊúÄÂ§ßÂèØÈÄâÊó•Êúü
			const today = new Date()
			this.today = today.toISOString().split('T')[0]
		},
		methods: {
			// ËÆ°ÁÆóÂπ¥ÈæÑÔºàÊúàÔºâ
			calculateAgeInMonths(birthDate) {
				if (!birthDate) return 0
				const birth = new Date(birthDate)
				const today = new Date()
				let ageInMonths = (today.getFullYear() - birth.getFullYear()) * 12
				ageInMonths += today.getMonth() - birth.getMonth()
				
				// Â¶ÇÊûúÊó•ÊúüËøòÊ≤°Âà∞ÔºåÂáè1‰∏™Êúà
				if (today.getDate() < birth.getDate()) {
					ageInMonths--
				}
				
				return Math.max(0, ageInMonths)
			},
			
			// Ëé∑ÂèñÂπ¥ÈæÑÊÆµÊñáÊú¨
			getAgeRangeText(ageInMonths) {
				if (ageInMonths < 3) return '1-3‰∏™Êúà'
				if (ageInMonths < 6) return '3-6‰∏™Êúà'
				if (ageInMonths < 9) return '6-9‰∏™Êúà'
				if (ageInMonths < 12) return '9-12‰∏™Êúà'
				if (ageInMonths < 18) return '1-1.5Â≤Å'
				if (ageInMonths < 24) return '1.5-2Â≤Å'
				if (ageInMonths < 30) return '2-2.5Â≤Å'
				if (ageInMonths < 36) return '2.5-3Â≤Å'
				if (ageInMonths < 48) return '3-4Â≤Å'
				if (ageInMonths < 60) return '4-5Â≤Å'
				return '5-6Â≤Å'
			},
			
			// ÈÄâÊã©ÊÄßÂà´
			selectGender(gender) {
				this.formData.gender = gender
			},
			
			// ÈÄâÊã©‰∏ªË¶ÅÁÖßÈ°æËÄÖ
			selectCaregiver(caregiver) {
				this.formData.caregiver = caregiver
			},
			
			// ÈÄâÊã©ÊòØÂê¶ÊúâÂèëËÇ≤ËøüÁºìÂè≤
			selectDelayHistory(hasDelay) {
				this.formData.hasDelayHistory = hasDelay
			},
			
			// Âá∫ÁîüÊó•ÊúüÊîπÂèò
			onBirthDateChange(e) {
				this.formData.birthDate = e.detail.value
			},
			
			// Ë∑≥ËΩ¨Âà∞ËØÑ‰º∞È°µÈù¢
			goToAssessment() {
				if (!this.isFormValid) {
					uni.showToast({
						title: 'ËØ∑ÂÆåÂñÑÂøÖÂ°´‰ø°ÊÅØ',
						icon: 'none'
					})
					return
				}
				
				// ‰øùÂ≠òÂÑøÁ´•‰ø°ÊÅØÂà∞Êú¨Âú∞Â≠òÂÇ®
				uni.setStorageSync('childInfo', this.formData)
				
				// Ë∑≥ËΩ¨Âà∞ËØÑ‰º∞È°µÈù¢
				uni.navigateTo({
					url: '/pages/assessment/assessment'
				})
			}
		}
	}
</script>

